body {
  background-color: #f7f7f7;
  margin: 0;
  font-family: Arial, sans-serif;
}

.go-back {
  text-align: center;
  padding-top: 20px;
}

.go-back a {
  color: #333;
  font-weight: bold;
  text-decoration: none;
}

.go-back a:hover {
  text-decoration: underline;
}

.form-container {
  height: calc(100vh - 60px);
  justify-content: center;
  align-items: center;
  display: flex;
}

.form {
  background-color: #fff;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

.form label {
  margin-bottom: 10px;
  display: block;
}

.form label:last-child {
  margin-bottom: 20px;
}

.form input[type="number"] {
  width: calc(100% - 20px);
  border: 1px solid #ddd;
  border-radius: 5px;
  margin-top: 5px;
  padding: 10px;
  font-size: 16px;
}

.form button {
  width: 100%;
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  padding: 10px;
  font-size: 18px;
  transition: background-color .3s;
}

.form button:hover {
  background-color: #45a049;
}

.form button:disabled {
  cursor: not-allowed;
  background-color: #ccc;
}

